import { getLongTime } from './getLongTime';

describe('getLongTime', () => {
  it('to return long time generated by Intl', async () => {
    expect(getLongTime('lt', new Date('2021-08-12 16:45'))).toMatch(
      /16:45; (GMT\+3|UTC)/
    );
  });

  describe('return long time which is modified', () => {
    it('Turkish (tr)', async () => {
      expect(getLongTime('tr', new Date('2021-08-12 16:45'))).toMatch(
        /Ã–S 4:45 (GMT\+3|UTC)/
      );
    });
  });
});
